// ========================================
//
// @file _locations.scss
// Locations pages specific styling
//
// ========================================
#block-locationbanner {
   .field--name-field-banner-title {
     @include banner-title;
   }
}
.user-logged-in {
  #block-locationbanner {
    .field--name-field-banner-title {
      @include banner-title;
      @media screen and (max-width: $screen-xs-min){
        width: 100%;
        left: 0;
        top: -10px;
      }
    }
  }
}
.block-views-exposed-filter-blocklocations-page-1 {
  @include exposed-filter-banner;
}
.page-node-type-location {
  .main-content {
    @extend .col-xs-9;
    display: inline-block;
    @media screen and (max-width: $screen-md-min) {
      width: 100%;
    }
  }
  #sidebar-second {
    @extend .col-xs-3;
    display: inline-block;
    position: relative;
    float: right;
    @media screen and (max-width: $screen-md-min) {
      width: 100%;
      float: none;
    }
  }
}
.location_page {
  .loc-name {
    display: block;
    border-bottom: 1px solid $gray-border;
    margin-bottom: 30px;
    h2 {
      display: inline-block;
      min-width: 100px;
      font-weight: 100;
      margin-bottom: 0;
      & >span {
        text-transform: capitalize;
      }
    }
    span {
      display: inline-block;
      padding-left: 10px;
      text-transform: lowercase;
      color: $color-black;
      @include rem('font-size', 30px);
    }
  }
  .description {
    .left-block {
      @extend .col-xs-4;
      display: inline-block;
      vertical-align: top;
      @media screen and (max-width: $screen-xs-min) {
        width: 100%;
      }
    }
    .right-block {
      @extend .col-xs-8;
      display: inline-block;
      vertical-align: top;
      .field--name-body {
        p {
          margin-top: 0;
        }

      }
      @media screen and (max-width: $screen-xs-min) {
        width: 100%;
      }
    }
  }
  .equipment {
    display: inline-block;
    width: 45%;
    float: right;
    margin: 18px 0;
    .field--name-field-equipment {
      .field__label {
        font-size: 24px;
        font-weight: 100;
      }
      .field__items {
        .field__item {
          display: inline-block;
          @include rem('font-size', 16px);
          text-transform: uppercase;
          background-color: $button-blue;
          color: white;
          padding: 8px;
          margin: 8px 5px;
        }
      }
    }
    @media screen and (max-width: $screen-md-min) {
      width: 100%;
    }
  }
  .info {
    display: inline-block;
    width: 45%;
    .web {
      @include info-single-block;
      .field--name-field-website {
        &:before {
          content: $fa-var-sitemap;
          font-family: FontAwesome;
          @include rem('font-size', 45px);
          color: $color-black;
          display: inline-block;
          position: relative;
          left: -67px;
          top: 3px;
        }
        &:after {
          content: '';
          display: block;
          position: relative;
          width: 3px;
          height: 20px;
          background-color: $button-blue;
          top: -25px;
          left: 0;
        }
        &:hover:before {
          color: $button-orange;
        }
        &:hover:after {
          background-color: $button-orange;
        }
        a {
          text-decoration: none;
          color: $color-black;
        }
      }
    }
    .phone {
      @include info-single-block;
      padding: 6px 0 20px 107px;
      .field--name-field-phone-number {
        &:before {
          content: $fa-var-mobile-phone;
          font-family: FontAwesome;
          @include rem('font-size', 45px);
          color: $color-black;
          display: inline-block;
          position: relative;
          left: -75px;
          top: 6px;
        }
        &:after {
          content: '';
          display: block;
          position: relative;
          width: 3px;
          height: 20px;
          background-color: $button-blue;
          top: -26px;
          left: -24px;
        }
        &:hover:before {
          color: $button-orange;
        }
        &:hover:after {
          background-color: $button-orange;
        }
      }
    }
    .mail {
      @include info-single-block;
      .field--name-field-contact-email {
        &:before {
          content: $fa-var-envelope-o;
          font-family: FontAwesome;
          @include rem('font-size', 45px);
          color: $color-black;
          display: inline-block;
          position: relative;
          left: -68px;
          top: 4px;
        }
        &:after {
          content: '';
          display: block;
          position: relative;
          width: 3px;
          height: 20px;
          background-color: $button-blue;
          top: -26px;
          left: 0;
        }
        &:hover:before {
          color: $button-orange;
        }
        &:hover:after {
          background-color: $button-orange;
        }
      }
    }
    @media screen and (max-width: $screen-md-min) {
      width: 100%;
      float: none;
    }
  }
  .loc {
    display: block;
    min-height: 230px;
    border: 2px solid $gray-border;
    padding: 10px;
    overflow: auto;
    .left-block {
      display: inline-block;
      width: 32%;
      padding-top: 20px;
      .field--name-field-company-address {
        display: inline-block;
        width: 150px;
        position: relative;
        float: right;
        padding: 5px 0 10px 10px;
        @include rem('font-size', 20px);
        &:before {
          content: $fa-var-briefcase;
          font-family: FontAwesome;
          @include rem('font-size', 50px);
          color: $color-black;
          display: inline-block;
          position: absolute;
          right: 179px;
          top: 17px;
        }
        &:after {
          content: '';
          display: block;
          position: relative;
          width: 3px;
          height: 98px;
          background-color: $button-blue;
          top: -108px;
          left: -21px;
        }
      }
      @media screen and (max-width: $screen-sm-min) {
        width: 100%;
        max-height: 150px;
        .field--name-field-company-address {
          float: left;
          clear: left;
          left: 100px;
        }
      }
    }
    .right-block {
      display: inline-block;
      width: 65%;
      float: right;
      .field--name-field-company-location {

      }
      @media screen and (max-width: $screen-sm-min) {
        float: none;
        width: 100%;
      }
    }
    &:hover {
      border: 2px solid $button-orange;
      .field--name-field-company-address {
        &:after {
          background-color: $button-orange;
        }
        &:before {
          color: $button-orange;
        }
      }
    }
  }
}