// ========================================
//
// @file _events.scss
// Companies pages specific styling
//
// ========================================


#block-companybanner {
  .field--name-field-banner-title {
    @include banner-title;
  }
}
.user-logged-in {
  #block-companybanner {
    .field--name-field-banner-title {
      @include banner-title;
      @media screen and (max-width: $screen-xs-min){
        width: 100%;
        left: 0;
        top: -10px;
      }
    }
  }
}
.page-node-type-company {
  .main-content {
    @extend .col-xs-9;
    display: inline-block;
    @media screen and (max-width: $screen-md-min) {
      width: 100%;
    }
  }
  #sidebar-second {
    @extend .col-xs-3;
    display: inline-block;
    position: relative;
    float: right;
    @media screen and (max-width: $screen-md-min) {
      width: 100%;
      float: none;
    }
  }
}
.block-views-exposed-filter-blockcompany-page-1 {
  @include exposed-filter-banner;
}
.company_page {
  .comp-name {
    display: block;
    border-bottom: 1px solid $gray-border;
    margin-bottom: 30px;
    h2 {
      display: inline-block;
      min-width: 100px;
      font-weight: 100;
      & >span {
        text-transform: capitalize;
      }
    }
    span {
      display: inline-block;
      padding-left: 10px;
      text-transform: lowercase;
      color: $color-black;
      @include rem('font-size', 30px);
    }
  }
  .description {
    .left-block {
      display: inline-block;
      vertical-align: top;
      width: 20%;
      float: left;
      margin: 10px;
      .field--name-field-location-image {
        width: 263px;
        height: 275px;
        overflow: hidden;
      }
    }
    .right-block {
      display: block;
      vertical-align: top;
      clear: right;
      .field--name-body {
        p {
          margin-top: 0;
        }

      }
    }
  }
  .info {
    display: block;
    .industry {
      @include info-single-block;
      display: inline-block;
      width: 45%;
      margin-right: 3%;
      padding: 1px 0 20px 87px;
      .field--type-entity-reference {
        &:before {
          content: $fa-var-folder;
          font-family: FontAwesome;
          @include rem('font-size', 45px);
          color: $color-black;
          display: inline-block;
          position: relative;
          left: -72px;
          top: 10px;
        }
        &:after {
          content: '';
          display: block;
          position: relative;
          width: 3px;
          height: 20px;
          background-color: $button-blue;
          top: -23px;
          left: -6px;
        }
        &:hover:before {
          color: $button-orange;
        }
        &:hover:after {
          background-color: $button-orange;
        }
        a {
          text-decoration: none;
          color: $color-black;
        }
      }
      @media screen and (max-width: $screen-sm-min){
        display: block;
        width: 100%;
        .field--type-entity-reference {
          &:before {
            @include rem('font-size', 40px);
          }
        }
      }
    }
    .web {
      @include info-single-block;
      display: inline-block;
      width: 45%;
      padding: 1px 0 20px 70px;
      .field--name-field-website {
        &:before {
          content: $fa-var-sitemap;
          font-family: FontAwesome;
          @include rem('font-size', 45px);
          color: $color-black;
          display: inline-block;
          position: relative;
          left: -59px;
          top: 10px;
        }
        &:after {
          content: '';
          display: block;
          position: relative;
          width: 3px;
          height: 20px;
          background-color: $button-blue;
          top: -23px;
          left: 11px;
        }
        &:hover:before {
          color: $button-orange;
        }
        &:hover:after {
          background-color: $button-orange;
        }
        a {
          text-decoration: none;
          color: $color-black;
        }
      }
      @media screen and (max-width: $screen-sm-min){
        display: block;
        width: 100%;
        .field--name-field-website {
          &:before {
            @include rem('font-size', 40px);
          }
        }
      }
    }
    .phone {
      @include info-single-block;
      display: inline-block;
      width: 45%;
      margin-right: 3%;
      padding: 1px 0 20px 107px;
      .field--name-field-phone-number {
        &:before {
          content: $fa-var-mobile-phone;
          font-family: FontAwesome;
          @include rem('font-size', 45px);
          color: $color-black;
          display: inline-block;
          position: relative;
          left: -79px;
          top: 10px;
        }
        &:after {
          content: '';
          display: block;
          position: relative;
          width: 3px;
          height: 20px;
          background-color: $button-blue;
          top: -23px;
          left: -25px;
        }
        &:hover:before {
          color: $button-orange;
        }
        &:hover:after {
          background-color: $button-orange;
        }
      }
      @media screen and (max-width: $screen-sm-min){
        display: block;
        width: 100%;
        .field--name-field-phone-number {
          &:before {
            @include rem('font-size', 40px);
          }
        }
      }
    }
    .mail {
      @include info-single-block;
      display: inline-block;
      width: 45%;
      padding: 1px 0 20px 70px;
      .field--name-field-contact-email {
        &:before {
          content: $fa-var-envelope-o;
          font-family: FontAwesome;
          @include rem('font-size', 45px);
          color: $color-black;
          display: inline-block;
          position: relative;
          left: -56px;
          top: 10px;
        }
        &:after {
          content: '';
          display: block;
          position: relative;
          width: 3px;
          height: 20px;
          background-color: $button-blue;
          top: -23px;
          left: 13px;
        }
        &:hover:before {
          color: $button-orange;
        }
        &:hover:after {
          background-color: $button-orange;
        }
      }
      @media screen and (max-width: $screen-sm-min){
        display: block;
        width: 100%;
        .field--name-field-contact-email {
          &:before {
            @include rem('font-size', 40px);
          }
        }
      }
    }
  }
  .loc {
    display: block;
    min-height: 230px;
    width: 93%;
    border: 2px solid $gray-border;
    padding: 14px 10px 0 10px;
    overflow: auto;
    .left-block {
      display: inline-block;
      width: 32%;
      padding-top: 20px;
      .field--name-field-company-address {
        display: inline-block;
        min-width: 150px;
        position: relative;
        float: right;
        right:-10px;
        padding: 5px 0 0 10px;
        @include rem('font-size', 18px);
        &:before {
          content: $fa-var-briefcase;
          font-family: FontAwesome;
          @include rem('font-size', 50px);
          color: $color-black;
          display: inline-block;
          position: absolute;
          right: 177px;
          top: 14px;
        }
        &:after {
          content: '';
          display: block;
          position: relative;
          width: 3px;
          height: 98px;
          background-color: $button-blue;
          top: -80px;
          left: -21px;
        }
      }
      @media screen and (max-width: $screen-sm-min) {
        width: 100%;
        max-height: 150px;
        .field--name-field-company-address {
          float: left;
          clear: left;
          left: 100px;
        }
      }
    }
    .right-block {
      display: inline-block;
      width: 65%;
      float: right;
      .field--name-field-company-location {

      }
      @media screen and (max-width: $screen-sm-min) {
        float: none;
        width: 100%;
      }
    }
    &:hover {
      border: 2px solid $button-orange;
      .field--name-field-company-address {
        &:after {
          background-color: $button-orange;
        }
        &:before {
          color: $button-orange;
        }
      }
    }
    @media screen and (max-width: $screen-sm-min){
      width: 100%;
    }
  }
}