// ========================================
//
// @file _sidebar-second.scss
// Sidebar-second layout specific styling
//
// ========================================
#sidebar-second {
  #block-views-block-events-sidebar-block-1 {
    .view-header {
      @include sub-title;
    }
      .view-content {
        .views-row {
          .field--name-field-image {
            display: block;
            width: 100%;
            max-height: 150px;
            overflow: hidden;
          }
          .event-box {
            display: block;
            height: 60px;
            h4 {
              margin: 25px 0 5px 0;
              font-weight: 100;
              padding-left: 8px;
              border-left: 4px solid $button-blue;
              white-space: nowrap;
              text-overflow: ellipsis;
              overflow: hidden;
              max-width: 245px;
              a {
                text-decoration: none;
                text-transform: uppercase;
                color: $color-black;
                font-size: 15px;
              }
            }
            .date-event-front {
              display: block;
              position: relative;
              color: $button-orange;
              font-size: 14px;
              text-transform: uppercase;
              padding-left: 10px;
              .time,
              .date {
                display: inline-block;
              }
            }
          }
        }
      }
  }
}