// ========================================
//
// @file _mixins.scss
// File containing all Sass mixins
//
// ========================================

// - - Output property and value in rem with px fallback
@mixin rem($property, $values) {
  $font-size: $base-font-size;
  $px-values: ();
  $rem-values: ();

  @each $value in $values {
    @if $value == 0 or $value == 0px {
      $px-values: join($px-values, 0);
      $rem-values: join($rem-values, 0);
    } @else if type-of($value) == number and not unitless($value) and (unit($value) == px) {
      $new-rem-value: $value / $font-size;
      $px-values: join($px-values, round($value));
      $rem-values: join($rem-values, #{$new-rem-value}rem);
    } @else if type-of($value) == number and not unitless($value) and (unit($value) == "%") {
      $px-values: join($px-values, #{$value});
      $rem-values: join($rem-values, #{$value});
    } @else if $value == auto {
      $px-values: join($px-values, auto);
      $rem-values: join($rem-values, auto);
    } @else {
      $px-values: join($px-values, round($value * $font-size));
      $rem-values: join($rem-values, #{$value}rem);
    }
  }

  #{$property}: $px-values;
  #{$property}: $rem-values;
}

// - - Clearfix
@mixin clearfix {
	zoom: 1;

	&:before,
	&:after {
		content: "\0020";
		display: block;
		height: 0;
		overflow: hidden;
	}

	&:after {
		clear: both;
	}
}

// - - Image for retina
@mixin image-2x ($image, $width, $height) {
	@media (-webkit-min-device-pixel-ratio: 1.5), (min-resolution: 1.5dppx) {
		background-image: url($image);
		background-size: $width $height;
	}
}

// - - CSS calc function
@mixin calc ($property, $expression) {
	#{$property}: calc(#{$expression});
}

// - - Placeholder color
@mixin placeholder ($color, $opacity: 1) {
	&::-webkit-input-placeholder {
		color: $color;
		opacity: $opacity;
	}
	&:-moz-placeholder {
		color: $color;
		opacity: $opacity;
	}
	&::-moz-placeholder {
		color: $color;
		opacity: $opacity;
	}
	&:-ms-input-placeholder {
		color: $color;
		opacity: $opacity;
	}
}

// - - CTA buttons

@mixin cta-button-blue {
			@include rem('font-size', 14px);
			text-transform: uppercase;
			text-decoration: none;
	        background-color: $button-blue;
			color: white;
			width: 125px;
			height: 35px;
			padding: 8px 15px;
			display: block;
			border-radius: 20px;
			text-align: center;
	&:hover {
		background-color: $button-blue-hov;
		transition: opacity 0.63s, color 0.32s, background-color 0.31s ease-out;
	}
}

@mixin cta-button-orange {
	@include rem('font-size', 14px);
	text-transform: uppercase;
	text-decoration: none;
	background-color: $button-blue;
	color: white;
	width: 125px;
	height: 35px;
	padding: 8px 15px;
	display: block;
	border-radius: 20px;
	text-align: center;
	&:hover {
		background-color: $button-orange-hov;
		transition: opacity 0.63s, color 0.32s, background-color 0.31s ease-out;
	}
}
// - - Style for title and subtitle for view-pages
@mixin sub-title {
	h2 {
		text-transform: uppercase;
		font-weight: 100;
	}
	p {
		@include rem('font-size', 18px);
		text-transform: uppercase;
		margin-top: 0px;
		position: relative;
		top: -26px;
	}
}
// - - Style for banner title/subtitle all pages except home-page
@mixin banner-title {
	display: block;
	position: absolute;
	top: 17%;
	max-width: 800px;
	text-align: center;
	left: 35%;
	text-transform: uppercase;
	h2 {
		color: $color-white;
		font-weight: 100;
		@include rem('font-size', 43px);
		margin-bottom: 0;
		@media screen and (max-width: $screen-sm-min){
			@include rem('font-size', 20px);
		}
	}
	h4 {
		color: $color-white;
		@include rem('font-size', 16px);
		font-weight: 100;
		margin-top: 0;
		@media screen and (max-width: $screen-sm-min){
			@include rem('font-size', 14px);
		}
	}
	@media screen and (max-width: $screen-sm-min){
		left: 29%;
	}
	@media screen and (max-width: $screen-xs-min){
		width: 100%;
		left: 0;
		top: 125px;
	}
}
@mixin info-single-block {
	display: block;
	width: 400px;
	height: 70px;
	padding: 6px 0 20px 86px;
	@include rem('font-size', 17px);
	border: 1px solid $gray-border;
	margin:20px 0;

	&:empty {
		display: none;
	}
	&:hover {
		border: 1px solid $button-orange;
		transition: opacity 0.63s, color 0.32s, background-color 0.31s ease-out;
	}
	@media screen and (max-width: $screen-sm-min) {
		display: block;
		width: 100%;
		@include rem('font-size', 14px);
	}
}
@mixin exposed-filter-banner {
	background-color: $color-gray;
	margin-top: 1px;
	.title-filter {
		@extend .col-xs-3;
		padding-left: 50px;
		p {
			font-size: 22px;
			text-transform: uppercase;
			color: white;
			margin-top: 15px;
			span {
				display: block;
				color: white;
				background-color: $button-orange;
				width: 49px;
				padding: 0 2px;
				font-size: 12px;
				margin-bottom: 3px;
			}
		}
		&:before {
			content: ' ';
			display: block;
			width: 3px;
			height: 34px;
			position: absolute;
			background-color: $button-orange;
			left: 25px;
			top: 27px;
		}
		&:after {
			content: ':';
			display: block;
			position: absolute;
			color: white;
			left: 190px;
			top: 24px;
			font-size: 23px;
		}

		@media screen and (max-width: $screen-sm-min) {
			width: 100%;
		}
	}
	.form--inline {
		@extend .col-xs-9;
		.form-item-title {
			display: inline-block;
			width: 320px;
			input {
				width: 320px;
				height: 45px;
				border-radius: 5px;
				border: none;
				text-transform: uppercase;
				padding-left: 20px;
				@media screen and (max-width: $screen-sm-min) {
					height: 35px;
				}
			}
		}
		details.form-item {
			display: inline-block;
			width: 320px;
			border-radius: 5px;
			background-color: white;
			summary {
				width: 320px;
				height: 45px;
				border-radius: 5px;
				border: none;
				text-transform: uppercase;
				color: $color-gray;
				padding-top: 12px;
				@media screen and (max-width: $screen-sm-min) {
					height: 35px;
				}
			}
			.details-wrapper {
				display: block;
				position: absolute;
				z-index: 10;
				background-color: white;
				border-bottom-left-radius: 5px;
				border-bottom-right-radius: 5px;
				border: 1px solid gray;
				border-top: 0;
			}
		}
		.form-actions {
			display: inline-block;
			input {
				width: 190px;
				height: 45px;
				border: none;
				border-radius: 5px;
				background-color: $button-orange;
				text-transform: uppercase;
				color: white;
				@media screen and (max-width: $screen-sm-min) {
					height: 35px;
				}
			}
		}
	}
}
@mixin front-icon {
	font-family: FontAwesome;
	font-size: 40px;
	display: inline-block;
	width: 60px;
	height: 55px;
	position: absolute;
	left: -80px;
	background-color: $color-blue;
	padding: 7px;
	color: $color-black;
}
@mixin hover-efec {
	transition: opacity 0.63s, color 0.32s, background-color 0.31s ease-out;
}