// ========================================
//
// @file _base.scss
// Base level tags and styles
//
// ========================================

::selection {
  background: lighten($color-black, 10%);
  color: $color-white;
}

::-moz-selection {
  background: lighten($color-black, 10%);
  color: $color-white;
}

html {
  box-sizing: border-box;
  font-family: $font-karla;
}

body {
  background: rgba(255, 255, 255, 0.8);
  font-smoothing: antialiased;
  #page-wrapper {
    overflow-x: hidden;
    overflow-y: scroll;
  }

  .layout-container {
    @extend .container;
    max-width: 1200px;
  }
  .layout-banner-container {
    width: 100%;
  }
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

img {
  display: block;
  height: auto;
  margin: 0;
  max-width: 100%;
  transition: 0.4s;
}
