//--------------------- Carousel -------------------------
#slick-views-home-page-carousel-block-1-1 {
  .slick-list {
    .slide__caption {
      margin: 0 auto;
      //margin-top: -50px;
      position: relative;
      top: -350px;
      width: 100%;
      left: 0;
      color: $color-white;
      text-transform: uppercase;
      text-align: center;
      h2 {
        color: $color-white;
        font-weight: 100;
        font-size: 43px;
        margin-bottom: 0;
      }
      h4 {
        color: $color-white;
        font-weight: 100;
        margin-top: 0;
      }
    }
  }
  ul.slick-dots {
    bottom: 190px;
    li {
      button:before {
        content: '';
        border-radius: 10px;
        border: 2px solid white;
        width: 15px;
        height: 15px;

      }
    }
    li.slick-active {
      button {
        &:after {
          content: '';
          background-image: url("../../../../../sites/default/files/default_images/image.png");
          background-repeat: no-repeat;
          display: block;
          position: relative;
          left: -36px;
          top:-95px;
          width: 80px;
          height: 85px;
          z-index: 50;
        }
      }
    }
  }
}
@media screen and (min-width: $screen-sm-min) and (max-width: $screen-sm-max) {
  #slick-views-home-page-carousel-block-1-1 {
    .slick-list {
      .slide__caption {
        top: -275px;
      }
    }
    ul.slick-dots {
      bottom: 210px;
      li.slick-active {
        button {
          &:after {
            display: none;
          }
        }
      }
    }
  }
}
@media screen and (max-width: $screen-sm-min) {
  #slick-views-home-page-carousel-block-1-1 {
    .slick-list {
      .slide__caption {
        top: -135px;
        h2 {
          font-size: 20px;
        }
        h4 {
          font-size: 14px;
        }
      }
    }
    ul.slick-dots {
      bottom: 117px;
      li.slick-active {
        button {
          &:after {
            display: none;
          }
        }
      }
    }
  }
}
//-------------------------- Events block ----------------------------
#events-front {
  display: block;
  background-color: $color-blue;
  min-height: 610px;

}
.block-views-blockevents-front-page-block-1 {
  @extend .container;
  display: block;
  max-width: 1200px;
  min-height: 510px;
  padding: 15px 0 10px 10px;
  h2 {
    text-transform: uppercase;
    font-weight: 100;
    margin: 40px 0 0 0;
  }
  .view-header h4 {
    font-weight: 100;
    margin: 0 0 30px 0;
    font-size: 18px;
    text-transform: uppercase;
  }
  .views-col {
    padding-right: 6%;
    .teaser {
      .event-box {
        display: block;
        min-height: 100px;
        background-color: white;
        padding-left: 5px;
        h4 {
          margin: 0 0 5px 0;
          padding-top: 15px;
          font-weight: 100;
          a {
            display: block;
            position: relative;
            max-width: 200px;
            margin: 0 auto;
            text-decoration: none;
            text-transform: uppercase;
            color: $color-black;
            font-size: 15px;
            white-space: nowrap;
            text-overflow: ellipsis;
            overflow: hidden;
          }
        }
        .date-event-front {
          display: block;
          position: relative;
          max-width: 200px;
          margin: 0 auto;
          color: $button-orange;
          font-size: 14px;
          .time,
          .date {
            display: inline-block;
          }
        }
      }
    }
  }
}
@media screen and (max-width: $screen-xs-max){
  .block-views-blockevents-front-page-block-1 {
    .views-col {
      width: 50%!important;
      padding-right: 17%;
      margin-bottom: 15px;
      .col-2,
      .col-4 {
        padding-left: 13%;
        padding-right: 0;
      }
    }
  }
}
//------------------------------ Featured companies --------------------
.block-views-blockfeatured-companies-block-1 {
  .views-row {
    @extend .col-xs-4;
    display: inline-block;
    //width: 30%;
    height: 130px;
    margin: 5px 0;
    .views-field-field-logo {
      display: inline-block;
      position: relative;
      float: left;
    }
    .views-field-title {
      display: inline-block;
      position: relative;
      float: right;
      width: 70%;
      span a {
        text-decoration: none;
        font-size: 20px;
        color: black;
      }
    }
    .views-field-body {
      display: inline-block;
      position: relative;
      float: right;
      width: 70%;
    }
  }
  .more-link {
    width: 170px;
    margin: 0 auto;
    a {
      font-size: 15px;
      text-transform: uppercase;
      text-decoration: none;
      color: white;
      padding: 8px 15px;
      background-color: $button-blue;
      border-radius: 20px;
      text-align: center;
      &:hover {
        background-color: $button-blue-hov;
      }
    }
  }
}
//@media (min-width: $screen-md-min) and (max-width: $screen-lg-min) {
//  .block-views-blockfeatured-companies-block-1 {
//    .views-row {
//      width: 45%;
//    }
//  }
//}
@media  (max-width: $screen-sm-max) {
  .block-views-blockfeatured-companies-block-1 {
    .views-row {
      width: 100%;
    }
  }
}
//------------------------ Main front page content ----------------------------------
#block-onboarding-content {
  .node--type-basic-page-front {
    .node__content {
      #front-main-block {
        @extend .col-xs-7;
        display: inline-block;
        .field--name-body {
          margin-bottom: 40px;
        }
        h2,h4 {
          text-transform: uppercase;
          font-weight: 100;
          margin: 0;
        }
      }
      .field--name-field-sidebar-fields {
        @extend .col-xs-4;
        display: inline-block;
        position: relative;
        float: right;
        .field__item {
          margin-bottom: 30px;
          max-width: 445px;
          h4 {
            margin: 0 0 5px 0;
            font-weight: 100;
          }
          &:before {
            @include front-icon;
            content: $fa-var-clipboard;
            padding-left: 11px;
          }
          &:hover:before {
            background-color: $button-orange;
            color: white;
            @include hover-efec;
          }
        }
        .field__item:first-child {
          &:before {
            @include front-icon;
            content: $fa-var-bar-chart-o;
          }
          &:hover:before {
            background-color: $button-orange;
            color: white;
            @include hover-efec;
          }
        }
        .field__item:last-child {
          &:before {
            @include front-icon;
            content: $fa-var-black-tie;
            padding-left: 13px;
          }
          &:hover:before {
            background-color: $button-orange;
            color: white;
            @include hover-efec;
          }
        }


      }
    }
  }
}
@media (max-width: $screen-sm-max) {
  #block-onboarding-content {
    .node--type-basic-page-front {
      .node__content {
        #front-main-block {
          display: block;
          width: 100%;
          margin-bottom: 25px;
        }
        .field--name-field-sidebar-fields {
          display: block;
          width: 100%;
          left: 83px;
          .field__item {
            max-width: 80%;
            margin-right: 10px;
          }
        }
      }
    }
  }
}