// ========================================
//
// @file _header.scss
// Header element on the page
//
// ========================================

#header {
  display: block;
  position: relative;
  min-height: 50px;
  background-color: $header-top;
  margin-bottom: 10px;
  #block-sitename {
    .field--name-body {
      padding-top: 1px;
      display: inline-block;
      width: 200px;
      float: left;
      p {
        @include rem('font-size', 15px);
        color: $color-gray;
      }
    }
  }
  #block-email {
    height: 50px;
    padding-left: 27px;
    border-left: 1px solid $header-top;
    p {
      &:before {
        content: $fa-var-envelope-o;
        font-family: 'FontAwesome';
        left: 4px;
        position: absolute;
        top: 14px;
        font-size: 19px;
      }
    }
  }
  #block-email,
  #block-phonenumber {
    display: inline-block;
    width: 150px;
    position: relative;
    float: right;
    p {
      @include rem('font-size', 15px);
      color: $color-gray;
    }
  }
  #block-email {
    margin-right: 50px;
  }
  #block-phonenumber {
    width: 125px;
    p {
      &:before {
        content: $fa-var-mobile-phone;
        font-family: 'FontAwesome';
        left: -15px;
        position: absolute;
        top: 15px;
        font-size: 25px;
      }
    }
  }
}
//  -------------------- Primary Menu -------------------
.primary_menu {
  .region-primary-menu {
    #block-onboarding-branding {
      @extend .col-xs-3;
      display: inline-block;
      position: relative;
      float: left;
      clear: left;
    }
  }
}