// ========================================
//
// @file _footer.scss
// Footer layout specific styling
//
// ========================================
//---------------------- Footer first --------------------//
.region-footer-first {
  min-height: 500px;
  background-image: url("../../../../../sites/default/files/default_images/home-o-shape.png");
  background-repeat: no-repeat;
  background-position: bottom right;
  &:after {
    content: '';
    background-image: url("../../../../../sites/default/files/default_images/home-b-shape.png");
    display: block;
    height: 113px;
    background-repeat: no-repeat;
    width: 90%;
    background-position: bottom right;
  }
  h2 {
    text-transform: uppercase;
    font-weight: 100;
    margin: 40px 0 0 0;
  }
  .view-header {
    h4 {
      font-weight: 100;
      margin: 0 0 30px 0;
      font-size: 18px;
      text-transform: uppercase;
    }
  }
}
@media (max-width: $screen-xs-min) {
  .region-footer-first:after {
    width: 80%;
  }
}
//---------------------- Footer second --------------------//
.region-footer-second {
  display: block;
  border-bottom: 1px solid $gray-border;
  border-top: 1px solid $gray-border;
  text-align: center;
  min-height: 200px;
  margin: 10px 0;
  padding-bottom: 10px;
  h2 {
    margin-bottom: 0;
    span {
      color: $button-orange;
    }
  }
  p {
    text-transform: uppercase;
    margin-top: 0;
    font-size: 14px;
  }
  .field--type-webform {
    display: inline-block;
    margin-top: 20px;
    width: 140px;
    padding: 8px 15px;
    background-color: $button-blue;
    border-radius: 20px;
    a {
      font-size: 15px;
      text-transform: uppercase;
      text-decoration: none;
      color: $color-white;
      width: 111px;
      height: 20px;
      display: block;
    }
    &:hover {
      background-color: $button-blue-hov;
    }
  }
  .field--name-field-contact-us {
    background-color: $button-orange;
    margin-left: 20px;
    &:hover {
      background-color: $button-orange-hov;
    }
  }
}
// --- only for front page
.path-frontpage {
  .region-footer-second {
    margin: 0 0 10px 0;
  }
}
//---------------------- Footer third --------------------//
.region-footer-third {
  padding: 80px 0 50px 0;
  #block-footerlogo {
    display: inline-block;
    width: 15%;
    min-height: 200px;
    vertical-align: top;
  }
  .menu--footer {
    display: inline-block;
    width: 30%;
    min-height: 200px;
  }
  #block-newsletter,
  #block-contact {
    display: inline-block;
    width: 23%;
    min-height: 200px;
    vertical-align: top;
    h2 {
      font-size: 18px;
    }
  }
  #block-contact p {
    font-size: 14px;
    text-transform: uppercase;
    color: $color-gray;
    span {
      color: black;
      font-weight: bold;
    }
  }
  #block-newsletter {
    .field--name-body p {
      font-size: 14px;
      text-transform: uppercase;
      color: $color-gray;
    }
    .field--name-field-newsletter-form {
      .form-type-email {
        display: inline-block;
        input {
          width: 227px;
        }
      }
      #edit-actions {
        display: inline-block;
        float: right;
        input {
          color: $color-white;
          background-color: $button-blue;
          border: none;
          width: 37px;
          height: 25px;
        }
      }
    }
  }
}
@media (min-width: $screen-md) and (max-width: $screen-lg) {
  .region-footer-third {
    padding: 80px 0 78px 0;
  }
}
@media (min-width: $screen-sm) and (max-width: $screen-sm-max) {
  .region-footer-third {
    #block-footerlogo {
      margin-left: 5%;
    }
    .menu--footer {
      width: 40%;
    }
    #block-contact {
      width: 35%;
    }
    #block-newsletter {
      display: block;
      width: 40%;
      margin: 0 auto;
      padding-top: 20px;
    }
  }
}
@media  (max-width: $screen-xs-max) {
  .region-footer-third {
    #block-footerlogo {
      width: 23%;
    }
    .menu--footer {
      width: 50%;
    }
    #block-contact {
      display: block;
      width: 320px;
      margin: 0 auto;
    }
    #block-newsletter {
      display: block;
      width: 320px;
      margin: 0 auto;
    }
  }
}
//---------------------- Footer fourth ---------------//
.region-footer-fourth {
  display: block;
  min-height: 100px;
  border-top: 1px solid $gray-border;
  #block-copyright {
    display: block;
    width: 300px;
    margin: 0 auto;
    margin-top: 20px;
    p { color: $color-gray;}
  }
}

