@media screen and (min-width: $screen-xs-max) and (max-width: $screen-sm-max) {
  body .path-frontpage .region-banner {
    height: 415px;
  }
}
@media screen and (min-width: $screen-md-min) and  (max-width: $screen-md-max) {
  body .path-frontpage .region-banner {
    height: 470px;
  }
}
@media screen and (min-width: $screen-lg-min){
  body .path-frontpage .region-banner {
    min-height: 600px;
  }
}
.path-frontpage {
  #main-wrapper {
    margin-top: -90px;
  }
}
//  --------------------Event-view page-------------------
.view-events {
  .view-header {
    @include sub-title;
  }
  .views-row {
    //@extend .row;
  }
  .views-col {
    .view-mode {
      max-width: 356px;
      padding: 0 3px;
      .event-box {
        padding: 42px 20px;
        text-align: center;
        background-color: $color-blue;
        .date-event {
          display: block;
          position: relative;
          max-width: 300px;
          margin: 0 auto;
          .time {
            @include rem('font-size', 50px);
          }
          .date {
            text-transform: uppercase;
          }
        }
        h4 {
          display: block;
          max-width: 225px;
          margin: 0 auto;
          margin-top: 25px;
          @include rem('font-size', 18px);
          font-weight: normal;
          white-space: nowrap;
          text-overflow: ellipsis;
          overflow: hidden;
          color: black;
        }
        .field--name-body {

        }
        a.read-button {
          @include cta-button-blue;
          margin: 20px auto;
        }
        &:before {
          content: '';
          width: 0;
          height: 0;
          border-left: 30px solid transparent;
          border-right: 30px solid transparent;
          border-bottom: 30px solid $color-blue;
          position: relative;
          top: -81px;
        }
      }
    }
    @media screen and (max-width: $screen-sm-min){
      width: 100%!important;
      .view-mode {
        margin: 0 auto;
        margin-bottom: 10px;
      }
    }
  }
  .col-2 .view-mode {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: reverse;
    -ms-flex-direction: column-reverse;
    flex-direction: column-reverse;
    .event-box {
      &:before {
        display: none;
      }
      &:after {
        content: '';
        width: 0;
        height: 0;
        border-left: 30px solid transparent;
        border-right: 30px solid transparent;
        border-top: 30px solid $color-blue;
        position: relative;
        bottom: -81px;
      }
    }
  }
}
//  --------------------Companies-view page-------------------
 .path-companies {
   #block-onboarding-page-title h1 {
     text-transform: uppercase;
   }
   #block-onboarding-content {
     .view-header {
       @include sub-title;
     }
     .views-row {
       position: relative;
       float: left;
       clear: left;
       min-height: 220px;
       padding: 20px 10px;


       .views-field-field-logo {
         display: inline-block;
         width: 25%;
         float: left;
         img {
           margin: 0 auto;
           padding-top: 20px;
         }
       }

       .right-block {
         display: inline-block;
         width: 70%;
         float: right;
         p {
           @include rem('font-size', 14px);
         }
         p.title {
           @include rem('font-size', 20px);
           border-left: 3px solid $button-blue;
           padding-left: 10px;
         }
         a {
           @include cta-button-blue;

           &:before {
             content: '';
             display: block;
             position: absolute;
             top: 0;
             left: 0;
             width: 100%;
             height: 100%;
           }
         }
       }

       &:hover {
         cursor: pointer;
         box-shadow: 1px 1px 12px 1px $color-gray;

         .right-block {

           p.title {
             border-left: 3px solid $button-orange;
           }
           a {
             background-color: $button-orange;
           }
         }
       }
     }
   }

 }
//  -------------------- Locations-view page-------------------//
 .view-locations {
   margin-bottom: 50px;
   .view-header {
     @include sub-title;
   }
   .views-row {
     @extend .col-xs-3;
     display: inline-block;
     position: relative;
     padding: 25px 17px;
     .location-box {
       height: 100px;
       background-color: $color-blue;
       padding: 20px;
       a {
         text-decoration: none;
         text-transform: uppercase;
         color: $color-black;
       }
       p {
         margin-top: 3px;
         color: $button-orange-hov;
       }
     }
     @media screen and (max-width: $screen-sm-min){
       width: 50%;
       padding-left: 10%;
     }
     @media screen and (max-width: $screen-xs-min){
       width: 100%;
     }
   }

 }
//------------------- Sign-up Form Page ----------------//
.path-webform {
  #main-wrapper {
    min-height: 340px;
  }
}
//------------------- Footer elements ------------------------//
.site-footer {
  display: block;
  position: relative;
  overflow: hidden;
  &:before {
    content: '';
    display: block;
    width: 772px;
    height: 392px;
    background-color: blue;
    position: absolute;
    bottom: 0;
    right: -460px;
    overflow: auto;
    border: 302px solid $button-blue;
    border-left: 377px solid white;
    border-right: 250px solid white;
    border-top: 250px solid white;
    z-index: -10;
  }
  &:after {
    content: '';
    display: block;
    width: 772px;
    height: 392px;
    background-color: blue;
    position: absolute;
    bottom: 99px;
    left: -513px;
    overflow: auto;
    border: 302px solid $button-orange;
    border-right: 377px solid white;
    border-left: 250px solid white;
    border-top: 250px solid white;
    z-index: -10;
  }
}
@media  (max-width: $screen-sm-min) {
  .site-footer:after {
    left: -570px;
  }
  .site-footer:before {
    right: -567px;
  }
}
// ------------------- Contact pages -------//
.path-webform {
  .webform-submission-sign-up-form {
    color: $color-black;
    .form-item-name {
      input {
        display: inline-block;
        max-width: 300px;
        height: 35px;
        padding-left: 10px;
        border: 2px solid $gray-border;
      }
    }
    .form-item-email-address {
      input {
        display: inline-block;
        max-width: 300px;
        height: 35px;
        padding-left: 10px;
        border: 2px solid $gray-border;
      }
    }
    .form-actions {
      input {
        color: white;
        text-transform: uppercase;
        background-color: $button-orange;
        padding: 10px 20px;
        border-radius: 50px;
        border-style: none;
        &:hover {
          background-color: $button-orange-hov;
        }
      }
    }
  }
  .webform-submission-form {
    color: $color-black;
    .form-item-name,
    .form-item-email,
    .form-item-subject {
      input {
        display: inline-block;
        max-width: 300px;
        height: 35px;
        padding-left: 10px;
        border: 2px solid $gray-border;
      }
    }
    .form-item-message {
      textarea {
        border: 2px solid $gray-border;
      }
    }
    .form-actions {
      input {
        color: white;
        text-transform: uppercase;
        background-color: $button-orange;
        padding: 10px 20px;
        border-radius: 50px;
        border-style: none;
        &:hover {
          background-color: $button-orange-hov;
        }
      }
    }
  }
}