// ========================================
//
// @file _sidebar-first.scss
// Sidebar-first layout specific styling
//
// ========================================
.region-sidebar-first {
  .block-views {
    .view-organizer-and-location {
      .view-header {
        h2, h4 {
          font-weight: 300;
          margin: 0;
        }
        h4 {
          @include rem('font-size', 18px);
          margin-bottom: 40px;
        }
      }
      .view-content {
        display: block;
        padding: 15px 15px 55px 15px;
        border: 1px solid $gray-border;
        margin: 20px 0;
        .views-field-field-location-image,
        .views-field-field-logo {
          display: inline-block;
          width: 50px;
          float: left;
          padding: 5px;
        }

        .views-field-title {
          display: inline-block;
          padding-left: 10px;
          border-left: 3px solid $button-orange;
          margin: 12px 0 0 8px;
          a{
            text-decoration: none;
            color: $color-black;
          }
        }
        .views-field-body {
          padding-top: 10px;
          p {
            font-size: 13px;
            letter-spacing: 0.5px;
            line-height: 19px;
          }
          a {
            display: block;
            position: relative;
            float: right;
            padding-top: 20px;
            text-decoration: none;
            color: $button-orange;
          }
        }
      }
    }


  }
  #block-views-block-organizer-and-location-block-2 {
    .views-field-title {
      border-left: 3px solid $button-blue;
    }
    .views-field-body {
      a {
        color: $button-blue;
      }
    }
  }
}