// ========================================
//
// @file _events.scss
// Events pages specific styling
//
// ========================================
.page-node-type-event {
  .main-content {
    @extend .col-xs-9;
    display: inline-block;
    float: right;
    clear: right;
    @media screen and (max-width: $screen-sm-max){
      width: 100%;
      float: none;
    }
  }
  #sidebar-first {
    @extend .col-xs-3;
    display: inline-block;
    position: relative;
    float: left;
    @media screen and (max-width: $screen-sm-max){
      width: 100%;
    }
  }

}
#block-onboarding-page-title .page-title {
  display: none;
}
#block-eventsbanner {
  .field--name-field-banner-title {
    @include banner-title;
  }
}
.user-logged-in {
  #block-eventsbanner {
    .field--name-field-banner-title {
      @include banner-title;
      @media screen and (max-width: $screen-xs-min){
        width: 100%;
        left: 0;
        top: -10px;
      }
    }
  }
}
.block-views-exposed-filter-blockevents-page-1 {
  @include exposed-filter-banner;
}
.node--type-event .event-round .field--name-field-image  {
  img {
    border-radius: 50%;
    width: 180px;
    height: 180px;
  }
}
.node--type-event {
  text-align: center;
  .event-round {
    display: block;
    max-width: 180px;
    margin: 0 auto;
    padding-top: 70px;
  }
  .event-title h2 {
    font-weight: 100;
    letter-spacing: -0.5px;
    margin-bottom: 5px;
  }
  .event-type {
    font-size: 14px;
    display: block;
    position: relative;
    max-width: 20%;
    border-left: 2px solid $button-blue;
    margin: 0 auto;
    margin-bottom: 20px;
  }
  .start,
  .end {
    display: inline-block;
    position: relative;
    width: 33%;
    border-left: 2px solid $button-blue;
    span,
    .date,
    .time {
      display: inline-block;
      span {width: 25px;}
    }
  }
  .start {
    border-left: 2px solid $button-orange;
  }
  .start,
  .end {
    .date {text-transform: uppercase;}
  }
  .event-body {
    text-align: left;
    padding: 20px 30px;
    p {
      @include rem('font-size', 12px);
      letter-spacing: 0.5px;
      line-height: 19px;
    }
  }
}