// ========================================
//
// @file _navigation.scss
// All the navigation elements on the page
//
// ========================================

  #block-onboarding-main-menu {
    @extend .col-xs-9;
    display: inline-block;
    float: right;
    clear: right;
    padding-bottom: 15px;
    input {
      display: none;
    }
    ul.menu > li {
      display: inline-block;
      position: relative;
      clear: left;
      zoom:1;
      padding: 0 15px;
      a {
        display: block;
        font-size: 15px;
        text-transform: uppercase;
        text-decoration: none;
        color: $color-gray;
        &:hover {
          color: $color-black;
        }
        &.is-active {
          color: $color-black;
        }
      }
    }
    ul.menu >li:last-child {
      display: inline-block;
      position: relative;
      float: right;
      clear: right;
      padding: 7px 35px;
      background-color: $button-blue;
      border-radius: 20px;
      margin-top: -7px;
      a {
        font-size: 15px;
        text-transform: uppercase;
        text-decoration: none;
        color: $color-white;
        display: block;
        height: 20px;
        &:hover {
          background-color: $button-blue-hov;
        }
      }
    }
  }
@media  (max-width: $screen-sm-max) {
  #block-onboarding-main-menu {
    top: -18px;
    ul.menu {
      box-shadow: -8px 5px 17px $gray-border;
      border-radius: 8px;
      li {
        width: 100%;
        float: none;
        padding: 5px 0;
        a {
          font-size: 16px;
        }
      }
      position: absolute;
      width:355px;
      padding: 15px 0 15px 40px;
      background: white;
      margin-left: -100px;
      list-style-type: none;
      -webkit-font-smoothing: antialiased;
      /* to stop flickering of text in safari */
      transform-origin: 0% 0%;
      transform: translate(170%, 0%);
      transition: transform 0.5s cubic-bezier(.62,.64,.54,.37);
    }
    ul.menu >li:last-child {
      display: inline-block;
      position: relative;
      float: none;
      padding: 0;
      background-color: inherit;
      margin-top: 0;
      a {
        font-size: 15px;
        font-weight: bold;
        text-transform: uppercase;
        text-decoration: none;
        color: $button-blue;
        display: block;
        &:hover {
          color: $button-blue-hov;
          background-color: inherit;
        }
      }
    }
    ul.contextual-links {
      li{
        padding: 0;
        a {
          font-size: small;
        }
      }
    }
  }
  #menuToggle {
    display: block;
    position: relative;
    top: 26px;
    left: 10px;
    z-index: 1;
    float: right;
    clear: right;
    -webkit-user-select: none;
    user-select: none;
    input {
      display: block;
      width: 40px;
      height: 32px;
      position: absolute;
      top: -7px;
      left: -5px;
      cursor: pointer;
      opacity: 0; /* hide this */
      z-index: 2; /* and place it over the hamburger */
      -webkit-touch-callout: none;
    }
    span {
      display: block;
      width: 33px;
      height: 4px;
      margin-bottom: 5px;
      position: relative;
      background: #cdcdcd;
      border-radius: 3px;
      z-index: 1;
      transform-origin: 4px 0px;
      transition: transform 0.5s cubic-bezier(0.77,0.2,0.05,1.0),
      background 0.5s cubic-bezier(0.77,0.2,0.05,1.0),
      opacity 0.55s ease;
    }
  }
  #menuToggle span:first-child {
    transform-origin: 0% 0%;
  }

  #menuToggle span:nth-last-child(2) {
    transform-origin: 0% 100%;
  }
  #menuToggle input:checked ~ span {
    opacity: 1;
    transform: rotate(45deg) translate(-2px, -1px);
    background: #232323;
  }
  #menuToggle input:checked ~ span:nth-last-child(3) {
    opacity: 0;
    transform: rotate(0deg) scale(0.2, 0.2);
  }
  #menuToggle input:checked ~ span:nth-last-child(2) {
    opacity: 1;
    transform: rotate(-45deg) translate(0, -1px);
  }
  #menuToggle input:checked ~ ul {
    transform: scale(1.0, 1.0);
    opacity: 1;
  }
}
