  #block-onboarding-footer {
    h2 {
      font-size: 18px;
    }
    ul.menu:not(.contextual-links) {
      margin-left: 0;
      display: block;
      width: 300px;
      list-style-type: none;
      columns: 2;
      -webkit-columns: 2;
      -moz-columns: 2;
      li {
        padding: 7px 0;
        display: inline-block;
        width: 145px;
        a {
          text-transform: uppercase;
          color: $color-gray;
          text-decoration: none;
          font-size: 14px;
        }
      }
    }
  }

